.NavItem {
    margin-right: 20px;
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
  }
  
  .NavLink {
    color: white;
    text-decoration: none;
    display: inline-block;
    position: relative;
  }
  
  .NavLink:after {
    content: "";
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: pink;
    color: pink;
    transform-origin: bottom right;
    transition: transform 0.25s ease-out;
  }
  
  .NavLink:hover:after {
    color: pink;
    border-radius: 10px;
    transform: scaleX(1);
    transform-origin: bottom left;
  }
  
  .NavLink:hover{
      color: pink;
  }
  
  .navbar-light .navbar-toggler-icon {
    background-image: url("data:image/svg+xml;..");
  }
  